<script>
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import RangeSlider from 'vue-range-slider'
// you probably need to import built-in style
import 'vue-range-slider/dist/vue-range-slider.css'

export default {
  components: {Footer, Header, RangeSlider},
  data() {
    return {
      sliderValue: 1,
      value: 0,
      activeIndex: 0,
      max: 12,
      isActive: false,
      userResponses: [],
      answer: [],
      activeAn: [],
      an_res: {},
      is_response: false,
      option: '',
      answers: [],
      iq_tests: []
    }
  },
  mounted() {
    this.iq_tests = [
      {
        "id": 1,
        "title": "Soroq orniga mos togri variantni toping?",
        "is_open": false,
        "is_custom": false,
        "is_many_variants": false,
        "help_text": "",
        "choices": [
          {
            "id": 1,
            "text": "ha"
          }
        ]
      }

    ]

  },
  methods: {
    getAns(item) {
      this.answer[this.sliderValue - 1] = {
        "question_id": this.$store.state.test_one[this.sliderValue - 1].id,
        "choice_id": item.id
      }

    },
    nextStart(test, i) {
      this.answer[this.activeIndex] = {
        "question_id": test.id,
        "choice_id": this.answers[i].id
      }
      if (this.activeIndex < this.$store.state.test_one.length) {
        this.activeIndex++
      }
      console.log(this.answer, "test")

    },
    endTest() {
      this.$axiosDefault.post(`test-result/`,
          {
            "answers": this.answer,
            "test_id": this.$route.params.id
          }
          ,
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
            }
          })
          .then(res => {
            this.an_res = res.data.result
            this.is_response = true
          })
    },
    getOnetest() {
      this.is_response = false
      this.activeIndex = 0
      this.answer = []
      this.answers = []

    },
    prevStart() {
      if (this.activeIndex > 0) {
        this.activeIndex--
      }
    }
  }
}
</script>
